*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
scroll-behavior: smooth;
}
body{
  background-color: black;
  overflow-x: hidden;
  position: relative;
}

:root {
  --header-height: 3.5rem;
/*========== Colors ==========*/
  /*Color mode HSL(hue, saturation, lightness)*/
  --first-color: #997642;

  --white-color: #fff;
  --text-color: hsl(22, 4%, 75%);
  --text-color-light: hsl(22, 4%, 55%);
  --body-color: hsl(22, 8%, 6%);
  --container-color: hsl(22, 8%, 7%);

  /*========== Font and typography ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/


  --biggest-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .5rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
}
/* Responsive typography */
@media screen and (min-width: 1024px) {
  :root {
    --biggest-font-size: 3.5rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}
p{
  font-family: 'Lexend Mega', sans-serif;
  font-size: var(--small-font-size);
}
h1{
  font-family: 'Montserrat', sans-serif;
  
}
h2{
  font-family: 'Montserrat', sans-serif;
  
}
h3{
  font-family: 'Montserrat', sans-serif;
  
}
h4{
  font-family: 'Montserrat', sans-serif;
  
}
h5{
  font-family: 'Montserrat', sans-serif;
  
}

  a {
    font-family: 'Butcherman', cursive;
    font-weight: 400;
    text-decoration: none;
    color: white;
    margin-right: 1rem;
  }
  a:hover{
    color: var(--first-color);
  }
  img {
    max-width: 100%;
    height: auto;
  }
  /*=============== REUSABLE CSS CLASSES ===============*/
.container {
  max-width: 1024px;
  margin-inline: 1.5rem;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

.section {
  padding-block: 5rem 1.5rem;
}

.section__title {
  position: relative;
  
  
  color: var(--first-color);
  font-size: 40px;
  font-weight: 500;
  text-align: center;
  font-weight: initial;
  
  color: var(--first-color);
  
  margin-bottom: 2.5rem;
}

.category{
  position: relative;
  background-image: url(./Archivos/Imagenes/fondoservicios.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.category__container{
  
  grid-template-columns: 328px;
  justify-content: center;
  align-items: center;
 
}

.category__card{
  position: relative;
  align-items: center;
  justify-content: center;
  background-color: var(--white-color);
  border-radius: 2rem;
  padding: 4.5rem 2.25rem 2rem;
  box-shadow: 0 4px 16px hsla(22, 10%, 2%, .3);
  text-align: center;
  transition: background .3s;
  margin-top: 80px;
}
.category__img{
  color: white;
  width: 120px;
  position: absolute;
  inset: 0;
  margin: 0 auto;
  top: -3.5rem;
  transition: transform .3s;
}
.category__title{
  font-size: var(--h2-font-size);
  font-weight: 400;
  color: var(--first-color);
  margin-bottom: .5rem;
}


.category__card:hover, .category__img{
  transform: translateY(-.5rem);
  
}

.banner{
  position: relative;
  top: 0;
  left: 0;
  
}
.row12{
